.approve-suggestion-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.approve-suggestion-content {
    background: #f2f2f2;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    max-width: 550px;
    width: 90%;
}

.approve-suggestion-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    font-family: Arial;
    margin-bottom: 10px;
}

.approve-suggestion-input {
    width: calc(100% - 20px);
    padding: 10px;
    height: 15px;
    font-family: Arial;
    border: 1px solid #ccc;
    border-radius: 4px;
    appearance: none;
    background-color: white;
    color: black;
    font-size: 14px;
    text-align: center;
}

.approve-suggestion-textarea {
    max-width: calc(100% - 20px);
    width: calc(100% - 20px);
    min-height: 20px;
    max-height: 130px;
    padding: 10px;
    border: 1px solid #ccc;
    font-family: Arial;
    border-radius: 6px;
    font-size: 14px;
    transition: border 0.3s ease-in-out;
    outline: none;
    field-sizing: content;
    resize: none;
}

.approve-suggestion-group {
    padding: 20px 20px;
    background-color: white;
    border-radius: 5px;
}

.approve-suggestion-group-text {
    padding: 20px 20px;
    background-color: #f2f2f2;
    border-radius: 5px;
}

.approve-suggestion-group-spacer {
    padding-top: 10px;
    background-color: transparent;
    border-radius: 5px;
}

.approve-suggestion-title {
    font-size: 20px;
    font-weight: bold;
    margin: 0;
    flex: 1;

    text-align: left;
    font-family: Arial;
}

.approve-suggestion-close {
    background: none;
    color: #ADADAD;
    border: none;
    font-size: 20px;
    width: 2.1%;
    font-weight: bold;
    cursor: pointer;
}

.approve-suggestion-title {
    font-size: 1.4rem;
    margin-bottom: 15px;
}

.approve-suggestion-label {
    display: block;
    font-size: 19px;
    font-family: Arial;
    font-weight: 500;
    margin-bottom: 10px;
    text-align: center;
}

.approve-suggestion-label-tc {
    display: block;
    color: #7F7F7F;
    font-size: 12px;
    font-family: Arial;
    width: 85%;
    font-weight: 300;
    margin: 0px auto 10px;
    /* This centers the span */
    text-align: center;
}

.approve-suggestion-actions {
    display: flex;
    justify-content: space-between;
    margin-top: 15px;
}

.approve-suggestion-button {
    padding: 10px 15px !important;
    border: none !important;
    border-radius: 6px !important;
    cursor: pointer !important;
    font-weight: bold !important;
    font-size: 14px !important;
    flex: 1 !important;
    margin: 0 5px !important;
    transition: transform 0.2s ease, background-color 0.2s ease !important;
}

.approve-suggestion-button.confirm {
    background-color: #7EAC89 !important;
    color: white !important;
}

.approve-suggestion-button.cancel {
    background-color: #CB6F6F !important;
    color: white !important;
}

.approve-suggestion-button:hover {
    transform: scale(1.05) !important;
}

.approve-suggestion-button:active {
    transform: scale(0.95) !important;
}

.approve-suggestion-buttons {
    margin-top: 18px;
    display: flex;
    justify-content: center;
}

.approve-suggestion-button-download {
    background: linear-gradient(to bottom,
            #B8D4C1 0%,
            /* Lighter shade of 7EAC89 */
            #91B299 25%,
            /* Mid shade */
            #7EAC89 50%,
            /* Given color */
            #5C8766 75%,
            /* Darker shade */
            #30503A 100%
            /* Deepest shade */
        );

    color: white;
    border: none;
    padding: 12px;
    margin-top: 2px;
    margin-left: auto;
    margin-right: 10px;
    width: 30%;
    font-size: 16px;
    font-weight: bolder;
    font-family: Arial;
    cursor: pointer;
    border-radius: 5px;
}

.approve-suggestion-button-cancel {
    background: linear-gradient(to bottom,
            #E6A8A8 0%,
            /* Lighter shade of CB6F6F */
            #D17D7D 25%,
            /* Mid shade */
            #CB6F6F 50%,
            /* Given color */
            #963D3D 75%,
            /* Darker shade */
            #5A2020 100%
            /* Deepest shade */
        );
    color: white;
    border: none;
    padding: 12px;
    margin-top: 2px;
    margin-left: 10px;
    margin-right: auto;
    width: 30%;
    font-size: 16px;
    font-weight: bolder;
    font-family: Arial;
    cursor: pointer;
    border-radius: 5px;
}

.approve-suggestion-text {
    text-align: center;
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 15px;
}